import { Injectable } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';
import { map, first } from 'rxjs/operators';

const API_URL = 'api/confmanager/credentials/';

@Injectable({
  providedIn: 'root'
})
export class CredentialService {

  constructor(private http: HttpClient) {}

  add(credential:{}) {
    return this.http.post<any>(API_URL + '/add/', credential)
      .pipe(
        first(),
        map( (data) => {
          return data
        } )
      )
  }

  get(id:number) {
    const params = new HttpParams()
    .set('id', id.toString());
    return this.http.get<any>(API_URL + '/edit/', { params })
      .pipe(
        first(),
        map( (data) => {
          return data.credential
        } )
      )
  }

  save(credential:{}) {
    return this.http.post<any>(API_URL + '/edit/', credential)
      .pipe(
        first(),
        map( (data) => {
          return data
        } )
      )
  }

}
